.sidebar {
  position: sticky;
  top: 0;
  height: 100vh;
  background-color: #f8f9fa;
  padding: 1rem;
  width: 240px;
  transition: width 0.3s ease, padding 0.3s ease;
  /*overflow-y: auto;*/
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

/* Bot√£o para recolher */
.sidebar-toggle {
  background: none;
  border: none;
  cursor: pointer;
  margin-bottom: 1rem;
}

/* Sidebar recolhido */
.sidebar.collapsed {
  width: 60px;
  padding: 1rem 0.5rem;
}

.sidebar.collapsed h2,
.sidebar.collapsed ul {
  display: none;
}

/* Sidebar items */
.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  cursor: pointer;
  padding: 0.4rem;
  border-radius: 4px;
  transition: background 0.3s;
}

.sidebar li:hover {
  background-color: #e9e9e9;
}

.sidebar li.active {
  background-color: #b91c1c;
  color: white;
  font-weight: bold;
}

/* RESPONSIVO - telas menores que 900px */
@media (max-width: 900px) {
  .sidebar {
    position: fixed;
    z-index: 20;
    left: 0;
    top: 60px; /* abaixo do header */
    height: calc(100vh - 60px);
  }

  .main-content {
    margin-left: 60px !important;
  }
}

/* CELULAR - sidebar vira drawer */
@media (max-width: 600px) {
  .sidebar {
    width: 0;
    padding: 0;
    overflow: hidden;
    box-shadow: none;
  }

  .sidebar.open-mobile {
    width: 180px;
    padding: 1rem;
    box-shadow: 2px 0 8px rgba(0, 0, 0, 0.2);
  }

  .main-content {
    margin-left: 0 !important;
  }

  .sidebar-toggle {
    position: absolute;
    left: 10px;
    top: 10px;
  }
}
