.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e3e3e3;
  height: 100%;
}

h4 {
  color: #b91c1c
}

/* h3 {
color: #b91c1c
} */


.login-form {
  padding: 29px;
  width: 300px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  background-color: #f5f5f5;
}

.login-form h2 {
  margin-bottom: 1rem;
}

.login-form input {
  display: block;
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}


.login-form button {
  background-color: #b91c1c;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.login-form button:hover {
  transform: scale(1.05);
}

.login-form button:active {
  background-color: #003f7f;
  transform: scale(0.95);
}

#logo_mantran {
  height: 30px;
}


