.header {
  background-color: #b91c1c;
  color: white;
  padding: 0.7rem;
  text-align: left;
  font-size: 20pt;
  height: 40px;
}

#header_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#header_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.userIconWrapper {
  margin-left: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.userIcon {
  color: white; /* Cor base */
  /* filter: drop-shadow(0px 0px 3px rgba(255, 255, 255, 0.6))
          drop-shadow(0px 0px 6px rgba(255, 255, 255, 0.3)); */
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.userIcon:hover {
  transform: scale(1.1);
  /* filter: drop-shadow(0px 0px 4px rgba(255, 255, 255, 0.9))
          drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.7)); */
}

.searchWrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.searchWrapper input {
  padding-right: 28px;
}

.searchIcon {
  position: absolute;
  right: 6px;
  cursor: pointer;
}
